.resumeEditorWrapper {
  background: #fff;
  width: 50%;
  padding: 40px 32px;
  box-sizing: border-box;
}

.resumeEditorHeading {
  display:flex;
  justify-content: space-between;
}

/* Media Queries */

@media only screen and (max-width: 1200px) {
  .resumeEditorWrapper {
    width: 100%;
  }
}